// 
import commonUtils from '../../utils/commonUtil';
const indexTpl = require('./index6.string');
commonUtils.render(indexTpl);

// vue
const Vue = require('../../libs/vue');
// appappv-modelinput
new Vue({
    el: '#app6',
    data: {
        title: '',
        // input 
        msg: '',
        info: '',
        // checkbox:checkboxvalue
        checkedNames: ['Mike'],
        // radio
        picked: '',
        // select
        selected: 'Mike',
        selected2: '1',
        selected3: ['020'],
        items: [
            {text: 'One', value: 'A'},
            {text: 'Two', value: 'B'},
            {text: 'Three', value: 'C'},
        ],
        selected4: 'B',
        // 
        age: 0,
        toDebounce: '',
        toLazy: ''
    },
    methods: {
    },
    computed: {
        info: function(){
            return `{\"msg\": \"${this.msg}\" }`;
        }
    }
});


